import React from "react";
import "./Header.css";
import Fade from "react-reveal/Fade";
import $ from "jquery";
import { FaBeer } from "react-icons/fa";

function Header() {
  function test() {
    var tabsNewAnim = $("#navbarSupportedContent");
    var selectorNewAnim = $("#navbarSupportedContent").find("li").length;
    var activeItemNewAnim = tabsNewAnim.find(".active");
    var activeWidthNewAnimHeight = activeItemNewAnim.innerHeight();
    var activeWidthNewAnimWidth = activeItemNewAnim.innerWidth();
    var itemPosNewAnimTop = activeItemNewAnim.position();
    var itemPosNewAnimLeft = activeItemNewAnim.position();
    $(".hori-selector").css({
      top: itemPosNewAnimTop.top + "px",
      left: itemPosNewAnimLeft.left + "px",
      height: activeWidthNewAnimHeight + "px",
      width: activeWidthNewAnimWidth + "px",
    });
    $("#navbarSupportedContent").on("click", "li", function (e) {
      $("#navbarSupportedContent ul li").removeClass("active");
      $(this).addClass("active");
      var activeWidthNewAnimHeight = $(this).innerHeight();
      var activeWidthNewAnimWidth = $(this).innerWidth();
      var itemPosNewAnimTop = $(this).position();
      var itemPosNewAnimLeft = $(this).position();
      $(".hori-selector").css({
        top: itemPosNewAnimTop.top + "px",
        left: itemPosNewAnimLeft.left + "px",
        height: activeWidthNewAnimHeight + "px",
        width: activeWidthNewAnimWidth + "px",
      });
    });
  }
  $(document).ready(function () {
    setTimeout(function () {
      test();
    });
  });
  $(window).on("resize", function () {
    setTimeout(function () {
      test();
    }, 500);
  });
  $(".navbar-toggler").click(function () {
    setTimeout(function () {
      test();
    });
  });
  return (
    <header>
      <img className="logo" src="/logo.png" alt="" />
      <nav id="barre-menu" class="navbar navbar-expand-lg navbar-mainbg">
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto">
            <div className="hori-selector">
              <div className="left"></div>
              <div className="right"></div>
            </div>
            <li className="nav-item">
              <a className="nav-link" href="javascript:void(0);">
                <i className="fas fa-tachometer-alt"></i>Contact
              </a>
            </li>
            <li className="nav-item active">
              <a className="nav-link" href="javascript:void(0);">
                <i className="far fa-address-book"></i>Ã€ propos
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="javascript:void(0);">
                <i className="far fa-clone"></i>CV
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="javascript:void(0);">
                <i className="far fa-calendar-alt"></i>Mon travail
              </a>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  );
}

export default Header;
